@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono&family=Noto+Sans&display=swap);
:root {
    /* Meui-Light color definitions */
    --meui-light-background-color: #FFFFFF;
    --meui-light-secondary-background-color: #F5F5F5;

    --meui-light-text-color: #171A20;
    --meui-light-text-color-lowopacity: #171A2019;

    --meui-light-disabled-text-color: #888888;

    /* Meui-Dark color definitions */
    --meui-dark-background-color: #3B3B3D;
    --meui-dark-secondary-background-color: #2E2E2E;

    --meui-dark-text-color: #FFFFFF;
    --meui-dark-text-color-lowopacity: #FFFFFF19;

    --meui-dark-disabled-text-color: #5C5C5C;

    /* Other Meui color definitions */
    --meui-blue-accent-color: #2273E6;

    --meui-text-link-color: #2196F3;
}

h1, h2 {
    font-weight: normal;
}

body {
    margin: 0;
    font-family: 'Noto Sans';
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #FFFFFF;
    background-color: var(--meui-light-background-color);
    color: #171A20;
    color: var(--meui-light-text-color);
}

#root.dark-mode {
    background-color: #3B3B3D;
    background-color: var(--meui-dark-background-color);
    color: #FFFFFF;
    color: var(--meui-dark-text-color);
}

code {
    font-family: 'JetBrains Mono';
}

.website-content {
    margin: 0em 2em;
}

.button {
    color: #171A20;
    color: var(--meui-light-text-color);
    text-decoration: none;
    font-size: 1.5em;
    border-color: #2273E6;
    border-color: var(--meui-blue-accent-color);
    border-width: 0.1em;
    border-style: solid;
    border-radius: 0.5em;
    padding: 0.4em 0.6em;
    margin: 0.5em;
    transition: background-color 0.125s ease-in-out;
    background-color: #F5F5F5;
    background-color: var(--meui-light-secondary-background-color);
}

.dark-mode .button {
    color: #FFFFFF;
    color: var(--meui-dark-text-color);
    background-color: #2E2E2E;
    background-color: var(--meui-dark-secondary-background-color);
}

.button:hover {
    background-color: #171A2019;
    background-color: var(--meui-light-text-color-lowopacity);
}

.dark-mode .button:hover {
    background-color: #FFFFFF19;
    background-color: var(--meui-dark-text-color-lowopacity);
}

.disabled {
    color: #888888;
    color: var(--meui-light-disabled-text-color);
}

.dark-mode .disabled {
    color: #888888;
    color: var(--meui-light-disabled-text-color);
}

a {
    color: #2196F3;
    color: var(--meui-text-link-color);
}

.large-margin-top {
    margin-top: 5em;
}
.website-header {
    height: 10em;
    width: 100vw;
    justify-content: center;
    display: flex;
    background-color: var(--meui-light-secondary-background-color);
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.dark-mode .website-header {
    background-color: var(--meui-dark-secondary-background-color);
}

.website-header-text {
    display: flex;
    align-items: center;
    font-size: 3em;
}

.header-cyber-logo {
    width: 2em;
    height: 2em;
    margin-right: 0.5em;
}

